@import "@/assets/scss/variables.scss";


















































































































































































.fe-table {
  overflow-x: auto;

  &__table {
    width: 100%;
    max-width: 100%;
    border-collapse: collapse;
  }

  &__th,
  &__td {
    padding: 8px 15px;
    text-align: left;
  }

  &__th {
    height: 40px;
    font-weight: bold;
    vertical-align: middle;
    background-color: $color-gray;

    &.is-sortable {
      cursor: pointer;
    }
  }

  &__td {
    height: 50px;
    border-bottom: 1px solid $color-gray;
  }

  &__icon-sort {
    color: $color-gray-dark;

    &.is-active {
      color: $color-blue;
    }
  }
}
