@import "@/assets/scss/variables.scss";
















































.operations-tabs {
  display: flex;

  &__item {
    font-size: 11px;
    font-weight: bold;
    line-height: 13px;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;

    &:hover,
    &.is-active {
      color: $color-blue;
    }

    &.is-active {
      pointer-events: none;
      cursor: default;
    }

    & + & {
      margin-left: 18px;
    }
  }
}
